import { formatTime } from "../../lib/utils";
import { ChatMessage } from "@/lib/api";

export function ChatMessages({
  messages,
  userID,
}: {
  messages: ChatMessage[];
  userID: string;
}) {
  return (
    <div className="flex-1 overflow-y-auto p-6 space-y-4">
      {messages.map((m) => {
        const isMine = m.userId === userID;
        return (
          <div key={m.id} className={isMine ? "text-right" : "text-left"}>
            {!isMine && (
              <div className="text-sm font-semibold">{m.username}</div>
            )}
            <div className="text-xs text-gray-400">
              {formatTime(new Date(m.timestamp))}
            </div>
            <div className="inline-block rounded-2xl px-4 py-2 bg-amber-100">
              {m.text}
            </div>
          </div>
        );
      })}
    </div>
  );
}
