"use client";

import { useUsername } from "./hooks/useUsername";
import { useChatSocket } from "./hooks/useChatSocket";
import { useNow } from "./hooks/useNow";
import { NamePrompt } from "./components/NamePrompt";
import { ChatMessages } from "./components/ChatMessages";
import { ChatInput } from "./components/ChatInput";
import { ChatHeader } from "./components/ChatHeader";
import { fetchChats } from "@/lib/api";

export default function ChatPage() {
  useNow();

  const { username, userID, showPrompt, saveName } = useUsername();
  const { messages, sendMessage } = useChatSocket(username);

  const FetchAvailableChats = async () => {
    const chats = await fetchChats();
    console.log(chats);
  };

  if (showPrompt) return <NamePrompt onSubmit={saveName} />;

  return (
    <div className="h-screen flex flex-col max-w-2xl mx-auto">
      <ChatHeader />
      <ChatMessages messages={messages} userID={userID} />
      <ChatInput
        onSend={(text) =>
          sendMessage({
            id: crypto.randomUUID(),
            userId: userID,
            username,
            text,
            timestamp: Date.now(),
          })
        }
      />
    </div>
  );
}
